.main {
  min-height: 100vh;
  display: grid;
  grid-template-rows: 1fr auto;
  grid-template-columns: 100%;
}

.content {
  max-width: 1024px;
  width: 80%;
  justify-self: center;
}

a {
  text-decoration: none;
}

p, h1, h2, h3 {
  margin: 0;
}

a:hover {
  color: #e00000;
  cursor: pointer;
}
